


































import { defineComponent } from '@vue/composition-api'
import { useSidebar } from '@/plugins/store/sidebar'

export default defineComponent({
    name: 'Navbar',

    setup () {
        const { isSidebarOpen, setSidebar } = useSidebar()

        function toggle () {
            setSidebar(!isSidebarOpen.value)
        }

        return {
            toggle
        }
    }
})
